import React from 'react';
import { Modal, Checkbox, Row, Col, message } from 'antd';
// import { postAction } from 'src/axios';

const CheckboxGroup = Checkbox.Group;

export default class ChooseModal extends React.Component {
    state = {
        checkAll: false,
        collegeLists: [],
    }

    componentDidMount(){
        this.setState({
            collegeLists: JSON.parse(JSON.stringify(this.props.collegeLists)) || []
        })
    }

    selectData(index) {
        let { collegeLists } = this.state; 
        collegeLists[index].active = !collegeLists[index].active;
        let arrLen = collegeLists.filter(item => !item.active);
        if(arrLen.length > 0){
            this.setState({collegeLists,checkAll:false})
        }else {
            this.setState({collegeLists,checkAll:true})
        }

        // postAction("/bigdata/alarmStrategy/checkCollegeHasAlarmStrategy", {strategyTarget: collegeCode}).then(
        //     (res) => {
        //         if (res.success) {
        //             if (res.obj) {
        //                 message.warn('学院已有预警策略配置');
        //             } else {
        //                 let { collegeLists } = this.state; 
        //                 collegeLists[index].active = !collegeLists[index].active;
        //                 let arrLen = collegeLists.filter(item => !item.active);
        //                 if(arrLen.length > 0){
        //                     this.setState({collegeLists,checkAll:false})
        //                 }else {
        //                     this.setState({collegeLists,checkAll:true})
        //                 }
        //             }
        //         }
        //     }
        // )
    }

    onCheckAllChange(){
       let res = {"success":true,"msg":"成功","obj":["100172,100180,100193,100202,100215,100225,100234,100242,100247,100259,100265,100272,100278,100285,100295,100304,100314,100319,100331,100337,100342,100482,100365,100381,57","100394","123"],"errorCode":null};
       if (res.success) {
            let { collegeLists } = this.state;
            if (res.obj && res.obj.length > 0) {
                let hasDone = res.obj.join().split(',');
                // 根据已有预警策略过滤可选对象
                collegeLists = collegeLists.map(item=>{
                    if((hasDone.indexOf(item.collegeCode)>-1) && item.active === false){
                        item.active = false;
                    }
                    return item;
                })
                this.setState({checkAll: false, collegeLists});
                message.warn('学院已有预警策略配置');
            } else {
                let { checkAll } = this.state;
                collegeLists = collegeLists.map(item=>{
                    item.active = checkAll?false:true;
                    return item;
                })
                this.setState({checkAll: !checkAll, collegeLists})
            }
        }

        // let collegeLists = this.state.collegeLists;
        // getAction(
        //     "/bigdata/alarmStrategy/getHasAlarmCollege",
        //     {
        //         id:this.props.editorId || ''
        //     }
        // ).then(
        //     (res) => {
        //         if (res.success) {
        //             if (res.obj && res.obj.length > 0) {
        //                 let hasDone=res.obj.join().split(',')
        //                 // 根据已有预警策略过滤可选对象
        //                 collegeLists=collegeLists.map(item=>{
        //                     if(hasDone.indexOf(item.collegeCode)>-1){
        //                         item.active=false
        //                     }
        //                     return item
        //                 })
        //                 this.setState({
        //                     collegeLists
        //                 })
        //                 message.warn('学院已有预警策略配置');
        //             } else {
        //                 collegeLists.active = !collegeLists.active;
        //             }
        //         }
        //     }
        // )
    }

     // 保存按钮
     onOk = () => {
        let { collegeLists } = this.state;
        collegeLists = collegeLists.filter(item => item.active);
        this.props.onOk(collegeLists);
    }

    render() {
        return (
            <Modal
                title={'选择预警策略对象'}
                width={1000}
                visible={this.props.visible}
                onOk={this.onOk}
                onCancel={this.props.onCancel}
            >
                <Checkbox
                    style={{float:"right",padding:"0px 0px 20px 0px"}}
                    indeterminate={this.state.indeterminate}
                    onChange={this.onCheckAllChange.bind(this)}
                    checked={this.state.checkAll}
                >
                    全选
                </Checkbox>
                <CheckboxGroup>
                    <Row gutter={10}>
                        {
                            this.state.collegeLists && this.state.collegeLists[0] && this.state.collegeLists.map((item, index) => (
                                <Col span={6} key={index}>
                                    <div style={{marginBottom: 10, textAlign: 'center', lineHeight: '30px', fontSize: '14px', overflow: 'hidden', textOverflow: 'ellipsis',  whiteSpace: 'nowrap',  cursor: 'pointer', background: item.active?'#d8eafc':'#f5f5f5', borderRadius: 4}}
                                    onClick={this.selectData.bind(this, index)}>{item.collegeName}</div>
                                </Col>
                            ))
                        }
                    </Row>
                </CheckboxGroup>
            </Modal>
        )
    }
}