import React from 'react';
import { Card, Row, Col, InputNumber, Switch, Icon } from 'antd';
// import { postAction } from 'src/axios';
import styles from '../../index.module.less';


export default class WangLuo extends React.Component {

    state = {
        data: {
            redVal: null,
            orangeVal: null,
            yellowVal: null,
            blueVal: null,
            openFlag: 1
        }
    }

    componentDidUpdate(prevProps) {
        if (JSON.stringify(prevProps) !== JSON.stringify(this.props)) {
            this.setState({data: this.props.alarmNetworkPara})
        }
    }

    changeSwitch(value){
        let { data } = this.state;
        data.openFlag = value?1:0;
        this.setState({data});
        if(value){
            this.props.onChange({alarmNetworkPara: data})
        }else {
            this.props.onChange({alarmNetworkPara: {
                redVal: null,
                orangeVal: null,
                yellowVal: null,
                blueVal: null,
                openFlag: 0
            }})
        }
    }

    changeValue = (name, value)=> {
        let { data } = this.state;
        data[name] = value;
        this.setState({data});
        this.props.onChange({alarmNetworkPara: data})
    }

    render(){
        let { redVal, orangeVal, yellowVal, blueVal, openFlag } = this.state.data;
        return (
            <Card
                className={styles.mainCard}
                title={<div style={{fontSize: 14}}>网络预警</div>}
                extra={
                    <Switch 
                        checkedChildren={<Icon type="check" />}
                        unCheckedChildren={<Icon type="close" />}
                        checked={openFlag ? true : false} 
                        onChange={this.changeSwitch.bind(this)} 
                    />
                }
                bodyStyle={{padding: '30px 24px 70px'}}
                style={{marginBottom: 20, background: openFlag ? '#fff' : 'rgba(141, 153, 176, 0.1)'}}
            >
                <Row gutter={20}>
                    <Col span={12} style={{marginBottom: 20, textAlign: 'right'}}>
                        <span className={styles.circlePoint} style={{background: '#e45a5a'}}></span>
                        <span style={{ marginRight: 10 }}>红色预警</span>
                        <InputNumber type={'number'} value={redVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('redVal', value)} /> 小时 
                    </Col>
                    <Col span={12} style={{marginBottom: 20}}>
                        <span className={styles.circlePoint} style={{background: '#f5a623'}}></span>
                        <span style={{ marginRight: 10 }}>橙色预警</span>
                        <InputNumber type={'number'} value={orangeVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('orangeVal', value)} /> 小时 
                    </Col>
                    <Col span={12} style={{textAlign: 'right'}}>
                        <span className={styles.circlePoint} style={{background: '#f8e71c'}}></span>
                        <span style={{ marginRight: 10 }}>黄色预警</span>
                        <InputNumber type={'number'} value={yellowVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('yellowVal', value)} /> 小时 
                    </Col>
                    <Col span={12}>
                        <span className={styles.circlePoint} style={{background: '#309cff'}}></span>
                        <span style={{ marginRight: 10 }}>蓝色预警</span>
                        <InputNumber type={'number'} value={blueVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('blueVal', value)} /> 小时 
                    </Col>
                </Row>
                <div className={styles.tip}>学生当月日均上网时长超过预警值，系统会触发预警机制。</div>
            </Card>
        )
    }
}