import React from 'react';
import { Card, Row, Col, InputNumber, Switch, Icon } from 'antd';
// import { postAction } from 'src/axios';
import styles from '../../index.module.less';


export default class GuaKe extends React.Component {

    state = {
        data: {
            redSubjectVal:null,
            redScoreVal:null,
            orangeSubjectVal:null,
            orangeScoreVal:null,
            yellowSubjectVal:null,
            yellowScoreVal:null,
            blueSubjectVal:null,
            blueScoreVal:null,    
            redRel:'or',
            orangeRel:'or',
            yellowRel:'or',
            blueRel:'or',
            openFlag:1
        }
    }

    componentDidUpdate(prevProps) {
        if (JSON.stringify(prevProps) !== JSON.stringify(this.props)) {
            this.setState({data: this.props.alarmFailPara})
        }
    }

    changeSwitch(value){
        let { data } = this.state;
        data.openFlag = value?1:0;
        this.setState({data});
        if(value){
            this.props.onChange({alarmFailPara: data})
        }else {
            this.props.onChange({alarmFailPara: {
                redSubjectVal:null,
                redScoreVal:null,
                orangeSubjectVal:null,
                orangeScoreVal:null,
                yellowSubjectVal:null,
                yellowScoreVal:null,
                blueSubjectVal:null,
                blueScoreVal:null,    
                redRel:'or',
                orangeRel:'or',
                yellowRel:'or',
                blueRel:'or',
                openFlag:0
            }})
        }
    }

    changeRelSwitch(name, value){
        let { data } = this.state;
        data[name] = value?'or':'and';
        this.setState({data});
        this.props.onChange({alarmFailPara: data})
    }

    changeValue = (name, value)=> {
        let { data } = this.state;
        data[name] = value;
        this.setState({data});
        this.props.onChange({alarmFailPara: data})
    }

    render(){
        let { redSubjectVal, redScoreVal, orangeSubjectVal, orangeScoreVal, yellowSubjectVal, yellowScoreVal, blueSubjectVal, blueScoreVal, redRel, orangeRel, yellowRel, blueRel, openFlag } = this.state.data;
        return (
            <Card
                className={styles.mainCard}
                title={<div style={{fontSize: 14}}>挂科预警</div>}
                extra={
                    <Switch 
                        checkedChildren={<Icon type="check" />}
                        unCheckedChildren={<Icon type="close" />}
                        checked={openFlag ? true : false} 
                        onChange={this.changeSwitch.bind(this)} 
                    />
                }
                bodyStyle={{padding: '30px 24px 70px'}}
                style={{marginBottom: 20, background: openFlag ? '#fff' : 'rgba(141, 153, 176, 0.1)'}}
            >
                <Row gutter={20}>
                    <Col span={24} style={{marginBottom: 20, textAlign: 'center'}}>
                        <span className={styles.circlePoint} style={{background: '#e45a5a'}}></span>
                        <span style={{ marginRight: 10 }}>红色预警</span>
                        <InputNumber type={'number'} value={redSubjectVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('redSubjectVal', value)} /> 科
                        <Switch 
                            checkedChildren={'或'}
                            unCheckedChildren={'且'}
                            size="small"
                            style={{margin:"0px 10px"}} 
                            disabled={openFlag ? false : true}
                            checked={redRel === 'or' ? true : false} 
                            onChange={this.changeRelSwitch.bind(this, 'redRel')} 
                        />
                        <InputNumber type={'number'} value={redScoreVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('redScoreVal', value)} /> 学分
                    </Col>
                    <Col span={24} style={{marginBottom: 20, textAlign: 'center'}}>
                        <span className={styles.circlePoint} style={{background: '#f5a623'}}></span>
                        <span style={{ marginRight: 10 }}>橙色预警</span>
                        <InputNumber type={'number'} value={orangeSubjectVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('orangeSubjectVal', value)} /> 科
                        <Switch 
                            checkedChildren={'或'}
                            unCheckedChildren={'且'}
                            size="small"
                            style={{margin:"0px 10px"}}
                            disabled={openFlag ? false : true} 
                            checked={orangeRel === 'or' ? true : false} 
                            onChange={this.changeRelSwitch.bind(this, 'orangeRel')} 
                        />
                        <InputNumber type={'number'} value={orangeScoreVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('orangeScoreVal', value)} /> 学分
                    </Col>
                    <Col span={24} style={{marginBottom: 20, textAlign: 'center'}}>
                        <span className={styles.circlePoint} style={{background: '#f8e71c'}}></span>
                        <span style={{ marginRight: 10 }}>黄色预警</span>
                        <InputNumber type={'number'} value={yellowSubjectVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('yellowSubjectVal', value)} /> 科
                        <Switch 
                            checkedChildren={'或'}
                            unCheckedChildren={'且'}
                            size="small"
                            style={{margin:"0px 10px"}} 
                            disabled={openFlag ? false : true}
                            checked={yellowRel === 'or' ? true : false} 
                            onChange={this.changeRelSwitch.bind(this, 'yellowRel')} 
                        />
                        <InputNumber type={'number'} value={yellowScoreVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('yellowScoreVal', value)} /> 学分 
                    </Col>
                    <Col span={24} style={{textAlign: 'center'}}>
                        <span className={styles.circlePoint} style={{background: '#309cff'}}></span>
                        <span style={{ marginRight: 10 }}>蓝色预警</span>
                        <InputNumber type={'number'} value={blueSubjectVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('blueSubjectVal', value)} /> 科
                        <Switch 
                            checkedChildren={'或'}
                            unCheckedChildren={'且'}
                            size="small"
                            style={{margin:"0px 10px"}} 
                            disabled={openFlag ? false : true}
                            checked={blueRel === 'or' ? true : false} 
                            onChange={this.changeRelSwitch.bind(this, 'blueRel')} 
                        />
                        <InputNumber type={'number'} value={blueScoreVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('blueScoreVal', value)} /> 学分
                    </Col>
                </Row>
                <div className={styles.tip}>学生入学以来累计挂科数量达到预警值，系统会触发预警机制。</div>
            </Card>
        )
    }
}