import React from 'react';
import moment from 'moment';
import { Card, Row, Col, Input, DatePicker, message, Tag, Button } from 'antd';
import IconFont from 'src/iconfont'
// import { postAction } from 'src/axios';
import ChooseModal from './ChooseModal';
import styles from '../../index.module.less';

const { RangePicker } = DatePicker;
const disabledDate = (current)=> {
    return current && current < moment().endOf('day');
}

export default class Index extends React.Component {

    state = {
        strategyName: '',
        strategyTargetLists:[],
        visible: false,
        collegeLists: []
    }

    componentDidMount() {
        this.getCollegeLists();
    }

    componentDidUpdate(prevProps) {
        if (JSON.stringify(prevProps) !== JSON.stringify(this.props)) {
            this.getStrategyName();
            this.getStrategyTargetLists();
        }
    }

    getStrategyName(){
        let { strategyName } = this.props;
        this.setState({strategyName});
    }

    getStrategyTargetLists(){
        let { collegeLists } = this.state;
        let strategyTargetLists = [];
        let strategyTarget = this.props.strategyTarget.split(',');
        collegeLists && collegeLists[0] && collegeLists.map(item => {
            strategyTarget.forEach(code => {
                if(item.collegeCode === code){
                    item.active = true;
                    strategyTargetLists.push(Object.assign({},item));
                }
            })

            return item;
        })
        this.setState({collegeLists, strategyTargetLists})
    }

    checkStrategyName(e) {
        let strategyName = e.target.value;
        if(!strategyName){
            message.warn('预警策略名称不能为空');
            return false;
        };
        this.props.onChange({strategyName});
        // postAction("/bigdata/alarmStrategy/checkName", {strategyName}).then(
        //     (res) => {
        //         if (res.success) {
        //             if(res.obj){
        //                 this.props.onChange({strategyName}); 
        //             }else {
        //                 message.warn('预警策略名称不能重复');
        //             }
                    
        //         }
        //     }
        // )
    }

    getCollegeLists(){
        let res = {"success":true,"msg":"成功","obj":[{"collegeName":"全部学院","collegeCode":""},{"collegeName":"建筑学院","collegeCode":"100172"},{"collegeName":"机械工程学院","collegeCode":"100180"},{"collegeName":"能源与环境学院","collegeCode":"100193"},{"collegeName":"信息科学与工程学院","collegeCode":"100202"},{"collegeName":"土木工程学院","collegeCode":"100215"},{"collegeName":"电子科学与工程学院","collegeCode":"100225"},{"collegeName":"数学学院","collegeCode":"100234"},{"collegeName":"自动化学院","collegeCode":"100242"},{"collegeName":"计算机科学与工程学院、软件学院","collegeCode":"100247"},{"collegeName":"物理学院","collegeCode":"100259"},{"collegeName":"生物科学与医学工程学院","collegeCode":"100265"},{"collegeName":"材料科学与工程学院","collegeCode":"100272"},{"collegeName":"人文学院","collegeCode":"100278"},{"collegeName":"经济管理学院","collegeCode":"100285"},{"collegeName":"电气工程学院","collegeCode":"100295"},{"collegeName":"外国语学院","collegeCode":"100304"},{"collegeName":"化学化工学院","collegeCode":"100314"},{"collegeName":"交通学院","collegeCode":"100319"},{"collegeName":"仪器科学与工程学院","collegeCode":"100331"},{"collegeName":"艺术学院","collegeCode":"100337"},{"collegeName":"法学院","collegeCode":"100342"},{"collegeName":"医学院","collegeCode":"100482"},{"collegeName":"公共卫生学院","collegeCode":"100365"},{"collegeName":"吴健雄学院","collegeCode":"100381"},{"collegeName":"网络空间安全学院","collegeCode":"57"},{"collegeName":"无锡分校","collegeCode":"100394"}],"errorCode":null};
        if(res.success){
            this.setState({
                collegeLists: res.obj.filter(item=>item.collegeCode !== '') || [],
            })
        }
        // postAction("/bigdata/school/listCollege", {}).then(
        //     (res) => {
        //         if (res.success) {
        //             this.setState({
        //                 collegeLists: res.obj.filter(item=>item.collegeCode!=='') || [],
        //             })
        //         }
        //     }
        // )
    }

    onOk(strategyTargetLists){
        this.setState({strategyTargetLists, visible: false}, () => {
            let strategyTarget = this.state.strategyTargetLists.map(item => item.collegeCode).join(',');
            let { collegeLists } = this.state;
            collegeLists.map(item => {
                if(strategyTarget.indexOf(item.collegeCode) > -1) {
                    item.active = true;
                }else {
                    item.active = false;
                }
                return item;
            })
            this.setState({collegeLists});
            this.props.onChange({strategyTarget});
        });
        

    }

    deleteStrategyTarget(code){
        let { strategyTargetLists } = this.state;
        strategyTargetLists = strategyTargetLists.filter(item => code !== item.collegeCode);
        this.setState({strategyTargetLists});
        let strategyTarget = strategyTargetLists.map(item => item.collegeCode).join(',');
        let { collegeLists } = this.state;
        collegeLists.map(item => {
            if(strategyTarget.indexOf(item.collegeCode) > -1) {
                item.active = true;
            }else {
                item.active = false;
            }
            return item;
        })
        this.setState({collegeLists});
        this.props.onChange({strategyTarget});
    }

    render(){
        return (
            <Card
                className={styles.mainCard}
                title={<div className={styles.cardTitle}>预警基本信息</div>}
                bordered={false}
                style={{marginBottom: 20}}
            >
                <Row gutter={20}>
                    <Col span={12}>
                        <p style={{marginBottom: 8}}>预警策略名称</p>
                        <Input value={this.state.strategyName} onBlur={this.checkStrategyName.bind(this)} onChange={(e) => {this.setState({strategyName: e.target.value})}}/>
                    </Col>
                    <Col span={12}>
                        <p style={{marginBottom: 8}}>预警策略时间</p>
                        <RangePicker
                            style={{width:'100%'}}
                            value={this.props.startDate?[moment(this.props.startDate), moment(this.props.endDate)]:undefined}
                            disabledDate={disabledDate}
                            format="YYYY-MM-DD"
                            onChange={(mt,value)=>{
                                let data = {startDate:value[0],endDate:value[1]}
                                this.props.onChange(data);
                            }}
                        />
                    </Col>
                    <Col span={24} style={{marginTop: 20}}>
                        <p style={{marginBottom:8}}>预警策略对象</p>
                        <div style={{lineHeight: '35px', marginBottom: 5}}>
                        {this.state.strategyTargetLists && this.state.strategyTargetLists[0] && this.state.strategyTargetLists.map((item,index)=>(
                            <Tag 
                                style={{height:28, lineHeight:'28px',color:'#4a4a4a'}}
                                key={index} 
                            >
                                {item.collegeName}
                                <IconFont style={{marginLeft: 5}} onClick={this.deleteStrategyTarget.bind(this, item.collegeCode)} type={'icon-cuowu'} />
                            </Tag>
                        ))}
                        </div>
                        <Button icon="plus" type={'primary'} onClick={()=>{this.setState({visible:true})}}>新增</Button>
                    </Col>
                </Row>
                {
                    this.state.visible
                    &&
                    <ChooseModal
                        visible={this.state.visible}
                        collegeLists={this.state.collegeLists}
                        onCancel={()=>this.setState({visible:false})}
                        onOk={this.onOk.bind(this)}
                    />
                }
            </Card>
        )
    }
}