import React from 'react';
import { Card, Row, Col } from 'antd';
// import { postAction } from 'src/axios';
import ShiLian from './ShiLian';
import WangLuo from './WangLuo';
import GuaKe from './GuaKe';
import TaoKe from './TaoKe';
import XiaoFei from './XiaoFei';
import YeGui from './YeGui';
import styles from '../../index.module.less';


export default class Index extends React.Component {

    state = {
        
    }

    componentDidMount() {
        
    }


    render(){
        return (
            <Card
                className={styles.mainCard}
                title={<div className={styles.cardTitle}>预警模块配置</div>}
                bordered={false}
                style={{marginBottom: 20}}
            >
                <Row gutter={20}>
                    {/* 失联预警 */}
                    <Col span={12}>
                        <ShiLian 
                            alarmMissPara={this.props.alarmMissPara}
                            onChange={this.props.onChange}
                        />
                    </Col>

                    {/* 网络预警 */}
                    <Col span={12}>
                        <WangLuo 
                            alarmNetworkPara={this.props.alarmNetworkPara}
                            onChange={this.props.onChange}
                        />
                    </Col>

                    {/* 挂科预警 */}
                    <Col span={12}>
                        <GuaKe 
                            alarmFailPara={this.props.alarmFailPara}
                            onChange={this.props.onChange}
                        />
                    </Col>

                    {/* 逃课预警 */}
                    <Col span={12}>
                        <TaoKe 
                            alarmCoursePara={this.props.alarmCoursePara}
                            onChange={this.props.onChange}
                        />
                    </Col>

                    {/* 消费预警 */}
                    <Col span={24}>
                        <XiaoFei 
                            alarmConsumePara={this.props.alarmConsumePara}
                            onChange={this.props.onChange}
                        />
                    </Col>

                    {/* 夜归预警 */}
                    <Col span={24}>
                        <YeGui 
                            alarmBackPara={this.props.alarmBackPara}
                            onChange={this.props.onChange}
                        />
                    </Col>
                </Row>
            </Card>
        )
    }
}