import React from 'react';
import { Card, Row, Col, InputNumber, Switch, Icon } from 'antd';
// import { postAction } from 'src/axios';
import styles from '../../index.module.less';


export default class XiaoFei extends React.Component {

    state = {
        data: {
            redMaxVal:null,
            redMinVal:null,
            orangeMaxVal:null,
            orangeMinVal:null,
            yellowMaxVal:null,
            yellowMinVal:null,
            blueMaxVal:null,
            blueMinVal:null,
            openFlag:1
        }
    }

    componentDidUpdate(prevProps) {
        if (JSON.stringify(prevProps) !== JSON.stringify(this.props)) {
            this.setState({data: this.props.alarmConsumePara})
        }
    }

    changeSwitch(value){
        let { data } = this.state;
        data.openFlag = value?1:0;
        this.setState({data});
        if(value){
            this.props.onChange({alarmConsumePara: data})
        }else {
            this.props.onChange({alarmConsumePara: {
                redMaxVal:null,
                redMinVal:null,
                orangeMaxVal:null,
                orangeMinVal:null,
                yellowMaxVal:null,
                yellowMinVal:null,
                blueMaxVal:null,
                blueMinVal:null,
                openFlag:0
            }})
        }
    }

    changeValue = (name, value)=> {
        let { data } = this.state;
        data[name] = value;
        this.setState({data});
        this.props.onChange({alarmConsumePara: data})
    }

    render(){
        let { redMaxVal, redMinVal, orangeMaxVal, orangeMinVal, yellowMaxVal, yellowMinVal, blueMaxVal, blueMinVal,  openFlag } = this.state.data;
        return (
            <Card
                className={styles.mainCard}
                title={<div style={{fontSize: 14}}>消费预警</div>}
                extra={
                    <Switch 
                        checkedChildren={<Icon type="check" />}
                        unCheckedChildren={<Icon type="close" />}
                        checked={openFlag ? true : false} 
                        onChange={this.changeSwitch.bind(this)} 
                    />
                }
                bodyStyle={{padding: '30px 24px 70px'}}
                style={{marginBottom: 20, background: openFlag ? '#fff' : 'rgba(141, 153, 176, 0.1)'}}
            >
                <Row gutter={20}>
                    <Col span={12} style={{marginBottom: 20, textAlign: 'right'}}>
                        <span className={styles.circlePoint} style={{background: '#e45a5a'}}></span>
                        <span style={{ marginRight: 10 }}>红色预警</span>
                        <span style={{ marginRight: 10 }}>高于</span>
                        <InputNumber type={'number'} value={redMaxVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('redMaxVal', value)} />
                        <span style={{ marginLeft: 10 }}>——</span>
                        <span style={{ margin: '0 10px' }}>低于</span>
                        <InputNumber type={'number'} value={redMinVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('redMinVal', value)} /> 元
                    </Col>
                    <Col span={12} style={{marginBottom: 20}}>
                        <span className={styles.circlePoint} style={{background: '#f5a623'}}></span>
                        <span style={{ marginRight: 10 }}>橙色预警</span>
                        <span style={{ marginRight: 10 }}>高于</span>
                        <InputNumber type={'number'} value={orangeMaxVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('orangeMaxVal', value)} />
                        <span style={{ marginLeft: 10 }}>——</span>
                        <span style={{ margin: '0 10px' }}>低于</span>
                        <InputNumber type={'number'} value={orangeMinVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('orangeMinVal', value)} /> 元
                    </Col>
                    <Col span={12} style={{textAlign: 'right'}}>
                        <span className={styles.circlePoint} style={{background: '#f8e71c'}}></span>
                        <span style={{ marginRight: 10 }}>黄色预警</span>
                        <span style={{ marginRight: 10 }}>高于</span>
                        <InputNumber type={'number'} value={yellowMaxVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('yellowMaxVal', value)} />
                        <span style={{ marginLeft: 10 }}>——</span>
                        <span style={{ margin: '0 10px' }}>低于</span>
                        <InputNumber type={'number'} value={yellowMinVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('yellowMinVal', value)} /> 元 
                    </Col>
                    <Col span={12}>
                        <span className={styles.circlePoint} style={{background: '#309cff'}}></span>
                        <span style={{ marginRight: 10 }}>蓝色预警</span>
                        <span style={{ marginRight: 10 }}>高于</span>
                        <InputNumber type={'number'} value={blueMaxVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('blueMaxVal', value)} />
                        <span style={{ marginLeft: 10 }}>——</span>
                        <span style={{ margin: '0 10px' }}>低于</span>
                        <InputNumber type={'number'} value={blueMinVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('blueMinVal', value)} /> 元
                    </Col>
                </Row>
                <div className={styles.tip}>学生当月日均消费低于/高于预警值，系统会触发预警机制。</div>
            </Card>
        )
    }
}