import React from 'react';
import { Card, Row, Col, InputNumber, Switch, Icon, TimePicker } from 'antd';
// import { postAction } from 'src/axios';
import moment from 'moment';
import styles from '../../index.module.less';


export default class YeGui extends React.Component {

    state = {
        data: {
            redVal: null,
            orangeVal: null,
            yellowVal: null,
            blueVal: null,
            startTime: null,
            endTime: null,
            openFlag: 1
        }
    }

    componentDidUpdate(prevProps) {
        if (JSON.stringify(prevProps) !== JSON.stringify(this.props)) {
            this.setState({data: this.props.alarmBackPara})
        }
    }

    changeSwitch(value){
        let { data } = this.state;
        data.openFlag = value?1:0;
        this.setState({data});
        if(value){
            this.props.onChange({alarmBackPara: data})
        }else {
            this.props.onChange({alarmBackPara: {
                redVal: null,
                orangeVal: null,
                yellowVal: null,
                blueVal: null,
                startTime: null,
                endTime: null,
                openFlag: 0
            }})
        }
    }

    changeValue = (name, value)=> {
        let { data } = this.state;
        if (name === 'startTime') {
            data['startTime'] = value?value.format('HH:mm'):null;
        } else if (name === 'endTime') {
            data['endTime'] = value?value.format('HH:mm'):null;
        } else {
            data[name] = value;
        }
        this.setState({data});
        this.props.onChange({alarmBackPara: data})
    }

    render(){
        let { redVal, orangeVal, yellowVal, blueVal, startTime, endTime, openFlag } = this.state.data;
        return (
            <Card
                className={styles.mainCard}
                title={<div style={{fontSize: 14}}>夜归预警</div>}
                extra={
                    <Switch 
                        checkedChildren={<Icon type="check" />}
                        unCheckedChildren={<Icon type="close" />}
                        checked={openFlag ? true : false} 
                        onChange={this.changeSwitch.bind(this)} 
                    />
                }
                bodyStyle={{padding: '30px 24px 70px'}}
                style={{marginBottom: 20, background: openFlag ? '#fff' : 'rgba(141, 153, 176, 0.1)'}}
            >
                <Row gutter={20}>
                    <Col span={12} style={{ textAlign: 'center' }}>
                        <div style={{ marginBottom: 20 }}>
                            夜归开始时间：
                            <TimePicker style={{ marginLeft: 10 }} placeholder={'夜归时间'} disabled={openFlag ? false : true} format="HH:mm" value={startTime ? moment(startTime, 'HH:mm') : null} onChange={(value) => this.changeValue('startTime', value)} />
                        </div>
                        <div>
                            夜归结束时间：
                            <TimePicker style={{ marginLeft: 10 }} placeholder={'未归时间'} disabled={openFlag ? false : true} format="HH:mm" value={endTime ? moment(endTime, 'HH:mm') : null} onChange={(value) => this.changeValue('endTime', value)} />
                        </div>
                    </Col>
                    <Col span={12}>
                        <Col span={12} style={{marginBottom: 20, textAlign: 'right'}}>
                            <span className={styles.circlePoint} style={{background: '#e45a5a'}}></span>
                            <span style={{ marginRight: 10 }}>红色预警</span>
                            <InputNumber type={'number'} value={redVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('redVal', value)} /> 次 
                        </Col>
                        <Col span={12} style={{marginBottom: 20}}>
                            <span className={styles.circlePoint} style={{background: '#f5a623'}}></span>
                            <span style={{ marginRight: 10 }}>橙色预警</span>
                            <InputNumber type={'number'} value={orangeVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('orangeVal', value)} /> 次 
                        </Col>
                        <Col span={12} style={{textAlign: 'right'}}>
                            <span className={styles.circlePoint} style={{background: '#f8e71c'}}></span>
                            <span style={{ marginRight: 10 }}>黄色预警</span>
                            <InputNumber type={'number'} value={yellowVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('yellowVal', value)} /> 次 
                        </Col>
                        <Col span={12}>
                            <span className={styles.circlePoint} style={{background: '#309cff'}}></span>
                            <span style={{ marginRight: 10 }}>蓝色预警</span>
                            <InputNumber type={'number'} value={blueVal} disabled={openFlag ? false : true} min={0} onChange={(value)=>this.changeValue('blueVal', value)} /> 次 
                        </Col>
                    </Col>
                </Row>
                <div className={styles.tip}>学生每月晚于夜归时间，且达到该月晚归/未归次数预警值，系统会触发预警机制。</div>
            </Card>
        )
    }
}