import React from 'react';
import { Card, Checkbox, Tooltip, Table, Popconfirm } from 'antd';
import IconFont from 'src/iconfont';
// import { postAction } from 'src/axios';
import AddOrEditModal from './AddOrEditModal';
import UploadModal from './UploadModal';
import styles from '../../index.module.less';

const alarmTypeLists = [
    { label: '失联预警', value: 'alarmMiss' },
    { label: '网络预警', value: 'alarmNetwork' },
    { label: '挂科预警', value: 'alarmFail' },
    { label: '消费预警', value: 'alarmConsume' },
    { label: '夜归预警', value: 'alarmBack' },
    { label: '逃课预警', value: 'alarmCourse' },
];
const alarmLevelLists = [
    { label: '红色预警', value: 'redLevel' },
    { label: '橙色预警', value: 'orangeLevel' },
    { label: '黄色预警', value: 'yellowLevel' },
    { label: '蓝色预警', value: 'blueLevel' },
];
const pushTypeLists = [
    {label: '短信推送',value: 'msgStatus'},
    {label: '邮件推送',value: 'mailStatus'}
]

export default class Index extends React.Component {

    state = {
        showAddOrEdit: false,
        showUpload: false,
        isEdit: false,
        dataSource: [],
        columns: [{
            title: '序号',
            dataIndex: 'sortIndex',
            width: "8%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '工号',
            dataIndex: 'jobNumber',
            width: "15%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '姓名',
            dataIndex: 'pushName',
            width: "15%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }
        , {
            title: '手机号',
            dataIndex: 'pushPhone',
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '邮箱',
            dataIndex: 'pushMail',
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '提醒模块',
            dataIndex: 'warnName',
            width: "15%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        },
        {
            title: '关联级别',
            dataIndex: 'warnName2',
            width: "15%",
            render: (text) => (
            	<span title={text}>{text}</span>
            )
        }, {
            title: '操作',
            dataIndex: 'handle',
            render: (text, record) => (
                <React.Fragment>
                    <span style={{color: '#309cff', marginRight: 10, cursor: 'pointer'}} onClick={this.handleEdit.bind(this, record)}>编辑</span>
                    <Popconfirm title="确认删除?" onConfirm={() => this.deleteAction(record)}>
                        <span style={{color: '#8d99b0', cursor: 'pointer'}}>删除</span>
                    </Popconfirm>
                </React.Fragment>
            )
        }],
        pagination: {
            current: 1, // 当前页数，
            pageSize: 10, // 每页条数
            total: 0, // 数据总条数
            showTotal: (total) => `共${total}条数据`, //显示数据总条数
            showQuickJumper: true // 显示页码快速跳转
        },
    }

    componentDidUpdate(prevProps) {
        if (JSON.stringify(prevProps) !== JSON.stringify(this.props)) {
            this.getData();
        }
    }

    getData(){
        let { alarmPushList } = this.props;
        let dataSource = [];
        alarmPushList && alarmPushList[0] && alarmPushList.forEach(item => {
            let { alarmMiss, alarmNetwork, alarmFail, alarmConsume, alarmBack, alarmCourse, redLevel, orangeLevel, yellowLevel, blueLevel } = item;
            let warnArr = [], warnName, warnModule = [], warnArr2 = [], warnName2, warnModule2 = [];

            if (alarmMiss) {
                warnModule.push('alarmMiss');
                warnArr.push('失联预警');
            }
            if (alarmNetwork) {
                warnModule.push('alarmNetwork');
                warnArr.push('网络预警');
            }
            if (alarmFail) {
                warnModule.push('alarmFail');
                warnArr.push('挂科预警');
            }
            if (alarmConsume) {
                warnModule.push('alarmConsume');
                warnArr.push('消费预警');
            }
            if (alarmBack) {
                warnModule.push('alarmBack');
                warnArr.push('夜归预警');
            }
            if (alarmCourse) {
                warnModule.push('alarmCourse');
                warnArr.push('逃课预警');
            }
            if (redLevel) {
                warnModule2.push('redLevel');
                warnArr2.push('红色预警');
            }
            if (orangeLevel) {
                warnModule2.push('orangeLevel');
                warnArr2.push('橙色预警');
            }
            if (yellowLevel) {
                warnModule2.push('yellowLevel');
                warnArr2.push('黄色预警');
            }
            if (blueLevel) {
                warnModule2.push('blueLevel');
                warnArr2.push('蓝色预警');
            }
            warnName = warnArr.join(',');
            warnName2 = warnArr2.join(',');
            item.warnModule = warnModule;
            item.warnModule2 = warnModule2;
            item.warnName = warnName;
            item.warnName2 = warnName2;
            dataSource.push(item);
        })
        this.setState({dataSource: (dataSource && dataSource[0] && dataSource.map((item, index) => {
            return {
                ...item,
                sortIndex: index + 1,
                key: index + 1
            }
        })) || []})
    }

    pushTypeChange(checkedValues){
        let pushData = {};
        pushTypeLists.forEach(item => {
            if (checkedValues.some(a => a === item.value)) {
                pushData[item.value] = 1;
            } else {
                pushData[item.value] = 0;
            }
        })
        this.props.onChange({
            alarmPushList: this.state.dataSource && this.state.dataSource[0] && this.state.dataSource.map(item=>({...item,...pushData}))
        })
    }
    
    // 编辑单条
    handleEdit(record){
        this.setState({
            editData: record,
            showAddOrEdit: true,
            isEdit: true
        })
    }

    deleteAction(record){
        let { dataSource } = this.state;
        dataSource.splice(record.sortIndex - 1, 1);
        this.setState({
            dataSource: (dataSource && dataSource[0] && dataSource.map((item, index) => {
                return {
                    ...item,
                    sortIndex: index + 1,
                    key: index + 1
                }
            }))||[],
        }, () => {this.props.onChange({alarmPushList: this.state.dataSource})});
    }

    handleConfirm(values, isEdit){
        let { alarmType, alarmLevel, pushPerson } = values;
        pushPerson.map(data=>{
            let warnArr = [], warnArr2 = [];
            alarmTypeLists.forEach(ele => {
                if (alarmType.some(a => a === ele.value)) {
                    warnArr.push(ele.label);
                    data[ele.value] = 1;
                } else {
                    data[ele.value] = 0;
                }
            })
            alarmLevelLists.forEach(ele => {
                if (alarmLevel.some(a => a === ele.value)) {
                    warnArr2.push(ele.label);
                    data[ele.value] = 1;
                } else {
                    data[ele.value] = 0;
                }
            })
            data.warnModule = alarmType;
            data.warnModule2 = alarmLevel;
            data.warnName = warnArr.join(',');
            data.warnName2 = warnArr2.join(',');
            return data;
        })
        
        if(isEdit){
            let { dataSource } = this.state;
            dataSource && dataSource[0] && dataSource.map((item, index) => {
                if(item.jobNumber === pushPerson[0].jobNumber){
                    dataSource[index] = {
                        sortIndex: dataSource[index].sortIndex, 
                        key: dataSource[index].key, 
                        ...pushPerson[0]
                    };
                }
                return item;
            })
            pushPerson = dataSource;
        }else {
            pushPerson = [...this.state.dataSource, ...pushPerson];
            pushPerson = (pushPerson && pushPerson[0] && pushPerson.map((item, index) => {
                return {
                    ...item,
                    sortIndex: index + 1,
                    key: index + 1
                }
            }))||[];
        }
        this.setState({
            dataSource: pushPerson,
            showAddOrEdit: false,
            isEdit: false
        }, () => {
            this.props.onChange({alarmPushList: pushPerson})
        });
        
    }

    uploadAction(){

    }

    handleTableChange = (pagination) => {
        this.setState({
            pagination: {
                current: pagination.current, // 当前页数，
                pageSize: pagination.pageSize, // 每页条数
                total: pagination.total, // 数据总条数
                showTotal: (total) => `共${total}条数据`,
                showQuickJumper: true // 显示页码快速跳转
            }
        })
    }

    render(){
        let pushType = [];
        let { dataSource } = this.state;
        if(dataSource && dataSource[0] && dataSource[0].msgStatus) {
            pushType.push('msgStatus');
        }
        if(dataSource && dataSource[0] && dataSource[0].mailStatus) {
            pushType.push('mailStatus');
        }
        return (
            <Card
                className={styles.mainCard}
                title={<div className={styles.cardTitle}>预警推送人及推送方式</div>}
                bordered={false}
                style={{marginBottom: 20}}
            >
                <div style={{display: 'flex'}}>
                    <p style={{marginRight: 10, fontSize: 16}}>推送方式：</p>
                    <Checkbox.Group value={pushType} onChange={this.pushTypeChange.bind(this)} disabled={this.state.dataSource.length === 0}>
                        {
                            pushTypeLists.map((item, index) => <Checkbox key={index} value={item.value}>{item.label}</Checkbox>)
                        }
                    </Checkbox.Group>
                </div>

                <hr style={{height: 1, margin: '20px 0', border: 'none', backgroundColor: '#ccc'}} />

                <div style={{display: 'flex', marginBottom: 10}}>
                    <p style={{flex: 1, margin: '0 10px 10px 0', fontSize: 16}}>推送人员：</p>
                    <p style={{flex: 1, textAlign: 'right'}}>
                        <Tooltip placement="top" title="添加">
                            <IconFont style={{marginRight:15,fontSize:20,color:'#1890ff',cursor:'pointer'}} type={'icon-tianjiachengyuan'} onClick={() => this.setState({ showAddOrEdit: true, isEdit: false })}/>
                        </Tooltip>
                        <Tooltip placement="top" title="导出">
                            <IconFont style={{marginRight:15,fontSize:20,color:'#1890ff',cursor:'pointer'}} type={'icon-wenjiandaochu'} />
                        </Tooltip>
                        <Tooltip placement="top" title="导入">
                            <IconFont style={{marginRight:15,fontSize:20,color:'#1890ff',cursor:'pointer'}} type={'icon-wenjiandaoru'} onClick={() => {this.setState({ showUpload: true })}} />
                        </Tooltip>
                        <Tooltip placement="top" title="下载">
                            <IconFont style={{fontSize:20,color:'#1890ff',cursor:'pointer'}} type={'icon-xiazai'} />
                        </Tooltip>
                    </p>
                </div>

                <Table
                    dataSource={this.state.dataSource}
                    columns={this.state.columns}
                    pagination={this.state.pagination}
                    size='middle'
                    bordered={true}
                    onChange={this.handleTableChange}
                />

                {
                    this.state.showAddOrEdit
                    && 
                    <AddOrEditModal
                        visible={this.state.showAddOrEdit}
                        alarmTypeLists={alarmTypeLists}
                        alarmLevelLists={alarmLevelLists}
                        curPushPerson={this.state.dataSource}
                        isEdit={this.state.isEdit}
                        editData={this.state.editData} 
                        onCancel={()=>this.setState({showAddOrEdit:false})}
                        onOk={this.handleConfirm.bind(this)}
                    />
                }

                {
                    this.state.showUpload
                    && 
                    <UploadModal
                        visible={this.state.showUpload}
                        onCancel={()=>this.setState({showUpload:false})}
                        onOk={this.uploadAction.bind(this)}
                    />
                }
            </Card>
        )
    }
}